var CityPicker = require('tingle-city-picker');
var Field = require('tingle-field');
var Context = require('tingle-context');
var Icon = require('tingle-icon');



class CityField extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            show: false,
            select: '',
            city: {}
        }
    }

    componentWillMount() {
        var t = this;
        t._resetState();
    }

    componentDidMount() {
        var t = this;
        t.field = t.refs.field;
        t.fieldEl = React.findDOMNode(t.field);
        t.back = t.refs.back;
        t.backEl = React.findDOMNode(t.back);

        t.fieldEl.addEventListener('click', t._handleClick.bind(t));
        t.backEl.addEventListener('click', t._handleBackClick.bind(t));
    }

    _cityArrToObj(str, arr) {
        var obj = {};
        obj[str] = []
        arr.forEach(function(name, index) {
             obj[str].push({
                name: name
             })
        });
        return obj;
    }

    _resetState() {
        var t = this;
        t.city = Object.keys(t.props.cityData);
        t.finish = false;
        t.count = 1;
        t.level = 1;
        t.data = [];
        t.setState({
            select: '',
            city: t._cityArrToObj('选择省份', t.city)
        });
    }

    _handleClick() {
        var t = this;
        t.setState({
            show: true
        });
        t._resetState();
    }

    _handleBackClick() {
        var t = this;
        t.setState({
            show: false
        });
        t._resetState();
        t.props.onCancel();
    }

    _finish() {
        var t = this;
        if (!t.finish) {
            t.props.onChange(t.data);
            t.finish = true;
            t.setState({
                show: false
            });
        }
    }

    _handlePick(info) {
        var t = this;
        t.data.push(info.name);
        t.setState({
            select: t.state.select + info.name
        }, function() {
            if (t.count == 1 && t.level < t.props.level) {
                t.count++;
                t.city = Object.keys(t.props.cityData[info.name]);
                t.cityName = info.name;
                if (t.city.length != 0) {
                    t.setState({
                        city: t._cityArrToObj('选择城市', t.city)
                    });
                }
                else {
                    t._finish();
                }
            }
            else if (t.count == 2 && t.level < t.props.level) {
                t.count++;
                t.city = t.props.cityData[t.cityName][info.name];
                if (t.city.length != 0) {
                    t.setState({
                        city: t._cityArrToObj('选择县区', t.city)
                    });
                }
                else {
                    t._finish();
                }
                
            }
            else {
                t._finish();
            }
            t.level++;
        });
    }

    render() {
        var t = this;
        var height = document.documentElement.clientHeight || window.innerHeight;
        return (
            <div className="tCityField">
                <Field ref="field" label={t.props.label} className="tBCf">
                    <div className="tCityFieldIcon">
                        <span className="selectCity">{t.props.value}</span>
                        <Icon className="tCityFieldRight" id="tingle-city-field-chevron-right"/>
                    </div>
                </Field>
                <CityPicker className="tBCd" show={t.state.show} offsetTop={60} city={t.state.city} onPick={t._handlePick.bind(t)}>
                    <div className="headBar tFBH">
                        <Icon className="tCityFieldLeft" ref="back" id="tingle-city-field-chevron-left"/>
                        <span className="headBarTitle">{t.props.label}</span>
                    </div>
                    <div className="msgBox tLH30 tBCf">{t.state.select}</div>
                </CityPicker>
            </div>
        );
    }
}

CityField.defaultProps = {
    onChange: Context.noop,
    onCancel: Context.noop,
    readOnly: false,
    value: '',
    level: Infinity,
    label: '选择城市',
    cityData: {}
}

// http://facebook.github.io/react/docs/reusable-components.html
CityField.propTypes = {
    onChange: React.PropTypes.func,
    onCancel: React.PropTypes.func,
    readOnly: React.PropTypes.bool,
    value: React.PropTypes.string,
    level: React.PropTypes.number,
    label: React.PropTypes.string,
    cityData: React.PropTypes.object
}

module.exports = CityField;


