// 引入
var classnames = require('classnames');
var GroupList = require('tingle-group-list');
var Picker = require('tingle-picker');
var Context = require('tingle-context');


class CityPicker extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            city: {},
            filter: ''
        };
        var t = this;
    }

    componentWillMount() {
        var t = this;
        t.city = t.props.city;
        t.filterList('');
    }

    componentDidMount() {
        var t = this;
    }

    componentWillReceiveProps(nextProps) {
        var t = this;
        t.city = nextProps.city;
        t.filterList('');
    }

    _renderGroupLists() {
        var t = this;
        var arr = []
        var groups = Object.keys(t.state.city);
        groups.forEach(function(group, i) {
            arr.push(<GroupList className={group} title={group} key={group}>
                        {t.state.city[group].map(function(city, i) {
                            return <div className="tLH44 tPL10" key={group + i} data-group={group} data-name={city.name} onClick={t.handleClick.bind(t)}>{city.name}</div>
                        })}
                    </GroupList>)
        });
        return arr;
    }

    handleClick(e) {
        var t = this;
        var target = e.currentTarget;
        var name = target.getAttribute('data-name');
        var group = target.getAttribute('data-group');
        t.props.onPick && t.props.onPick(t.props.city[group].filter(function(ele) {return ele.name == name})[0]);
    }

    handleSearch(value) {
        var t = this;
        t.filterList(value);
        t.setState({
            filter: value
        })
    }

    handleFieldClick() {
        var t = this;
    }

    /**
     * 判断是否是汉字
     */

    _isChinese(str) {
        return /^[\u4e00-\u9fa5]+$/.test(str);
    }

    filterList(value) {
        var t = this;
        var city = t.city;
        var groups = Object.keys(city);
        var newCity = {};
        if (t.props.enableFilter) {
            if (t._isChinese(value)) {
                groups.forEach(function(group, i) {
                    var groupCity = city[group].filter(function(ele) {
                        return ele.name.indexOf(value) != -1
                    })
                    if (groupCity.length != 0) {
                        newCity[group] = groupCity;
                    }
                });
            }
            else {
                groups.forEach(function(group, i) {
                    var groupCity = city[group].filter(function(ele) {
                        return ele.abbr.indexOf(value) != -1 || ele.pinyin.indexOf(value) != -1
                    })
                    if (groupCity.length != 0) {
                        newCity[group] = groupCity;
                    }
                });
            }
        }
        else {
            newCity = city;
        }
        t.setState({
            city: newCity
        })
    }

    render() {
        var t = this;
        // 基于 Picker 扩展，没有外层包裹容器
        var {show, offsetTop, offsetBottom} = t.props;
        return (
                <Picker ref="picker" className={classnames({
                    'tCityPicker': true,
                    [t.props.className]: !!t.props.className
                })} onSearch={t.handleSearch.bind(t)}  filter={t.state.filter} show={show} offsetTop={offsetTop} offsetBottom={offsetBottom} showSearchBar={t.props.enableFilter}>
                    <Picker.List>
                        {t._renderGroupLists()}
                    </Picker.List>
                    {t.props.children}
                </Picker>
        )
    }
}

CityPicker.defaultProps = {
    show: false, // 是否显示，直接传给 picker
    offsetTop: 0, // 上方预留的空间，直接传给 picker
    offsetBottom: 0, // 下方预留的空间，直接传给 picker
    city: {}, // 城市列表
    enableFilter: false, // 是否有内置的搜索过滤条
    onPick: Context.noop // 选中之后的回调
}

// http://facebook.github.io/react/docs/reusable-components.html
CityPicker.propTypes = {
    show: React.PropTypes.bool,
    offsetTop: React.PropTypes.number,
    offsetBottom: React.PropTypes.number,
    city: React.PropTypes.object,
    enableFilter: React.PropTypes.bool,
    onPick: React.PropTypes.func
}

module.exports = CityPicker;


