class Icon extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
            <svg className={"tIcon " + this.props.className}
             dangerouslySetInnerHTML={{__html: '<use xlink:href="#' + this.props.id + '"/>'}}/>
        );
    }
}

module.exports = Icon;


